              </div>
              <!-- <div class="flex md4 sm12 xs12 px-1">
                <label class="input-label"> {{$t('placeholder.price')}} </label>
                <va-input v-model="UintAndPriceByCUstomer.valuePrice" type="number" step="0.5" :min="UintAndPriceByCUstomer.valueUint.min_Sale_Price === undefined? 0:UintAndPriceByCUstomer.valueUint.min_Sale_Price " :max="UintAndPriceByCUstomer.valueUint.max_Sale_Price=== undefined? '' :UintAndPriceByCUstomer.valueUint.max_Sale_Price"
