                  :error="numircriquierdvalidation(UintAndPriceByCUstomer.valueQuantaty,UintAndPriceByCUstomer.valueUint.min_Quantity)"
                  :error-messages="$t('errorMessage.Number')"
                  :placeholder="$t('placeholder.quantity')" required/>
              </div> -->
              <div class="flex md12 sm12 xs12 px-1">
                <p>
                  {{ (UintAndPriceByCUstomer.valueUint.customerPrice > 0 ?$t("placeholder.customerprice") + ':' + UintAndPriceByCUstomer.valueUint.customerPrice :$t("placeholder.customerdidntbuy") )+ " | " + $t("placeholder.AvarageBuyPrice")+ ':' + UintAndPriceByCUstomer.valueUint.avarage_Price_Inv_Count }}
                </p>
