<script>
import Vue from 'vue'
import * as spinners from 'epic-spinners'
import BillService from '../../../../services/BIll/BillService'
import { debounce } from 'lodash'
import helper from '../../../../helper/helper'

export default {
  components: {
    ...spinners,
  },
  props: {
    CustomerId: Number,
  },
  data () {
    return {
      searchNameTerm: null,
      searchBarCodeTerm: null,
      stopBtn: false,
      dataTable: [],
      loading: true,
      loadingProduct: false,
      showGroupProduct: false,
      groupProduct: [],
      groupsLevels: [],
      modelTitle: '',
      modelOktext: '',
      createModalProductPrice: false,
      editModalProductPrice: false,
      deleteModal: false,
      submitModdal: false,
      countDownModdal: false,
      UintAndPriceByCUstomer: {
        valueUint: '',
        valuePrice: 0,
        valueQuantaty: 1,
        currentproduct: '',
        discountByPercentage: 0.0,
        discountByPound: 0.0,
        options: [],
      },
      opjectBeforeEdit: {},
      currentIndex: '',
      currentobject: {},
      ApiRequest: {
        inv_Subs: [],
      },
      totalBilWithoutTax: 0,
      totalBilTax: 0,
      totalTax: 0,
      totalDiscount: 0,
      tableItems: [],
      countDown: 3,
      invDiscountByPercentage: 0,
      invDiscountByPound: 0,
      invDiscount: 0,
      invId: 0,
      enableInvDiscuont: true,
    }
  },
  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: 'fullName',
        title: this.$t('tables.headings.name'),
      }, {
        name: '__slot:quantity',
        title: this.$t('tables.headings.quantity'),
      },
      {
        name: 'uints',
        title: this.$t('tables.headings.units'),
      }, {
        name: '__slot:price',
        title: this.$t('tables.headings.price'),
      }, {
        name: '__slot:disscountPercentage',
        title: this.$t('tables.headings.disscountPercentage'),
      },
      {
        name: 'fullPriceWithoutDiscount',
        title: this.$t('tables.headings.fullPriceWithoutDiscount'),
      }, {
        name: 'fullPrice',
        title: this.$t('tables.headings.fullPrice'),
      }, {
        name: 'tax',
        title: this.$t('tables.headings.tax'),
      }, {
        name: 'priceAfterTax',
        title: this.$t('tables.headings.priceAfterTax'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  watch: {
    'UintAndPriceByCUstomer.valueUint': function (val) {
      this.UintAndPriceByCUstomer.valuePrice = val.price
      this.UintAndPriceByCUstomer.valueQuantaty = val.min_Quantity
    },
    tableItems: function (val) {
      this.totalBilWithoutTax = 0.0
      this.totalBilTax = 0.0
      this.totalTax = 0.0
      this.totalDiscount = 0.0

      this.tableItems.forEach(element => {
        if (element.disscountPercentage !== null) {
          this.enableInvDiscuont = false
          this.totalDiscount = parseFloat(this.totalDiscount + ((element.quantity * element.price) - element.priceAfterTax))
        }
        this.totalBilWithoutTax = parseFloat(this.totalBilWithoutTax + element.fullPrice)
        this.totalBilTax = parseFloat(this.totalBilTax + element.priceAfterTax)
        this.totalTax = parseFloat(this.totalTax + element.tax)
      })
    },
    searchNameTerm: function (val) {
      if (!this.searchNameTerm || this.searchNameTerm.length < 1) {
        this.showGroupProduct = false
        return this.groupProduct
      } else {
        this.showGroupProduct = true
        this.loadingProduct = true

        this.groupProduct = this.dataTable.allProducts.filter(item => {
          return item.nameAr.toLowerCase().includes(this.searchNameTerm.toLowerCase())
        })
        this.loadingProduct = false
      }
    },
    searchBarCodeTerm: function (val) {
      if (!this.searchBarCodeTerm || this.searchBarCodeTerm.length < 1) {
        this.showGroupProduct = false
        return this.groupProduct
      } else {
        this.showGroupProduct = true
        this.loadingProduct = true
        this.groupProduct = this.dataTable.allProducts.filter(item => {
          if (item.shortCutCode !== null && item.shortCutCode !== undefined) {
            return item.shortCutCode.toLowerCase().includes(this.searchBarCodeTerm.toLowerCase())
          } else {
            return false
          }
        })
        this.loadingProduct = false
      }
    },
    invDiscountByPercentage: function (val) {
      if (val > 0) {
        this.totalDiscount = (val / 100) * this.totalBilTax
        this.invDiscount = val
      }
    },
    invDiscountByPound: function (val) {
      if (val > 0) {
        this.totalDiscount = val
        this.invDiscount = (val / this.totalBilTax) * 100
      }
    },
  },
  created () {
    this.getnew()
  },
  methods: {
    computedStyle (AvVStock, MinStock) {
      if (AvVStock - 10 > MinStock) {
        return {
          backgroundColor: this.$themes.primary,
        }
      } else if (AvVStock > MinStock) {
        return {
          backgroundColor: this.$themes.gray,
        }
      } else {
        return {
          backgroundColor: this.$themes.danger,
        }
      }
    },
    floatingbtncomputedStyle () {
      if (Vue.i18n.locale() === 'eg' || Vue.i18n.locale() === 'ir') {
        return {
          position: 'fixed',
          width: '60px',
          height: '60px',
          bottom: '23px',
          left: '23px',
          'background-color': '#0C9',
          color: '#FFF',
          'border-radius': '50px',
          'text-align': 'center',
          'box-shadow': '2px 2px 3px #999',
          'z-index': '100',
        }
      } else {
        return {
          position: 'fixed',
          width: '60px',
          height: '60px',
          bottom: '23px',
          right: '23px',
          'background-color': '#0C9',
          color: '#FFF',
          'border-radius': '50px',
          'text-align': 'center',
          'box-shadow': '2px 2px 3px #999',
          'z-index': '100',
        }
      }
    },
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    fixPercentage (percentage) {
      if (percentage === null) {
        return 0
      }
      try {
        return parseFloat(percentage).toFixed(1)
      } catch (error) {
        console.error(error)
        return percentage
      }
    },
    search: debounce(function (searchNameTerm) {
      this.searchNameTerm = searchNameTerm
    }, 800),
    searchBarcode: debounce(function (searchBarCodeTerm) {
      this.searchBarCodeTerm = searchBarCodeTerm
    }, 800),
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },
    numircriquierdvalidationMin (data, min) {
      if (min) {
        if (data >= min) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    numircriquierdvalidationMax (data, max) {
      if (max) {
        if (data <= max) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    getnew () {
      BillService.getNewBill(this.CustomerId, 1)
        .then(response => {
          console.log(response.data)
          this.dataTable = response.data
          this.groupsLevels.push(response.data.groups)
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getProductByGroupId (group, rootLevel) {
      this.loadingProduct = true

      this.groupProduct = this.dataTable.allProducts.filter(item => {
        return (item.products_Groups !== null && item.products_Groups.id === group.id)
      })
      this.loadingProduct = false
      if (group.parentGroups.length > 0) {
        this.groupsLevels.splice(rootLevel + 1)
        this.groupsLevels.push(group.parentGroups)
      } else if (this.groupsLevels.length >= rootLevel) {
        this.groupsLevels.splice(rootLevel + 1)
      }
      console.log('product', this.groupProduct)
      console.log('this.nextLevelGroup', this.groupsLevels)
      this.showGroupProduct = true
      return this.groupProduct
    },
    ShowBestSellProductAgain () {
      this.showGroupProduct = false
    },
    addProduct (product) {
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      console.log(product)
      this.loadingProduct = true
      BillService.getUintAndPriceByCUstomer(product.id, this.CustomerId)
        .then(response => {
          console.log('getUintAndPriceByCUstomer', response.data)
          this.UintAndPriceByCUstomer.options = response.data
          if (product.fK_DeafultUnits !== null) {
            var defultUintPrice = this.UintAndPriceByCUstomer.options.find(x => x.fk_Uint === product.fK_DeafultUnits)
            if (defultUintPrice !== null && defultUintPrice !== undefined) {
              this.UintAndPriceByCUstomer.valueUint = defultUintPrice
              this.UintAndPriceByCUstomer.valuePrice = defultUintPrice.price
              this.UintAndPriceByCUstomer.valueQuantaty = defultUintPrice.min_Quantity
            } else if (this.UintAndPriceByCUstomer !== null && this.UintAndPriceByCUstomer !== undefined) {
              this.loadingProduct = false
              return this.showToast(this.$t('generic.didntHaveUints'), {
                icon: 'fa-exclamation',
                position: 'bottom-center',
                duration: 2500,
                type: 'error',
              })
            } else {
              this.UintAndPriceByCUstomer.valueUint = this.UintAndPriceByCUstomer.options[0]
              this.UintAndPriceByCUstomer.valuePrice = this.UintAndPriceByCUstomer.options[0].price
              this.UintAndPriceByCUstomer.valueQuantaty = this.UintAndPriceByCUstomer.options[0].min_Quantity
            }
          } else {

          }
          this.UintAndPriceByCUstomer.currentproduct = product
          // this.createModalProductPrice = true
          this.okCreatefunProductPrice()
          this.loadingProduct = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    updateQuantityValue (rowIndex, rowData, event) {
      var oldObject = this.tableItems[rowIndex]
      if (oldObject.product.avilableInStock < rowData.newquantity) {
        var check = confirm(this.$t('generic.invOutOfStockMsg'))
        if (check) {
          return this.showToast(this.$t('generic.canceelAddMsg'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      }
      if (rowData.newquantity <= 0) {
        var ch = confirm(this.$t('generic.invalidMsg'))
        if (ch) {
          return this.showToast(this.$t('generic.deletIenvItemMsg'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      }
      this.UintAndPriceByCUstomer.options = rowData.productToEdite.options
      this.UintAndPriceByCUstomer.valueUint = rowData.productToEdite.options.find(el => el.fk_Uint === rowData.uintid)
      this.UintAndPriceByCUstomer.valuePrice = rowData.price
      this.UintAndPriceByCUstomer.valueQuantaty = rowData.newquantity
      this.UintAndPriceByCUstomer.currentproduct = rowData.product
      this.currentIndex = rowIndex

      var newItem = {}
      newItem.uintid = this.UintAndPriceByCUstomer.valueUint.fk_Uint
      newItem.isPercentage = oldObject.isPercentage
      newItem.disscountPercentage = oldObject.disscountPercentage

      if (rowData.disscountPercentage !== null) {
        if (rowData.isPercentage) {
          this.UintAndPriceByCUstomer.discountByPercentage = rowData.disscountPercentage
          newItem.fullPrice = oldObject.disscountPercentage === null ? (this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity) : ((this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity) - ((oldObject.disscountPercentage / 100) * (this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity)))
        } else {
          this.UintAndPriceByCUstomer.discountByPound = (rowData.disscountPercentage / 100) * (rowData.price * rowData.quantity)
          newItem.disscountPercentage = ((this.UintAndPriceByCUstomer.discountByPound / (rowData.price * rowData.newquantity)) * 100)
          newItem.fullPrice = (this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity) - this.UintAndPriceByCUstomer.discountByPound
        }
      } else {
        newItem.fullPrice = oldObject.disscountPercentage === null ? (this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity) : ((this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity) - ((oldObject.disscountPercentage / 100) * (this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity)))
      }
      newItem.fullPriceWithoutDiscount = (this.UintAndPriceByCUstomer.valuePrice * rowData.newquantity)
      newItem.fullName = this.UintAndPriceByCUstomer.currentproduct.nameAr
      newItem.quantity = rowData.newquantity
      newItem.uints = this.UintAndPriceByCUstomer.valueUint.uint_Name
      newItem.price = this.UintAndPriceByCUstomer.valuePrice
      newItem.productToEdite = Object.assign({}, this.UintAndPriceByCUstomer)
      newItem.qu = rowData.newquantity
      newItem.productid = this.UintAndPriceByCUstomer.currentproduct.id
      newItem.product = this.UintAndPriceByCUstomer.currentproduct
      newItem.tax = ((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) * (newItem.fullPrice)).toFixed(2)
      newItem.taxValue = (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100)
      newItem.priceAfterTax = (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax ? (newItem.fullPrice) : (newItem.fullPrice) + (((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) === 0 ? 1 : (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100)) * (newItem.fullPrice))).toFixed(2)
      newItem.stockid = this.UintAndPriceByCUstomer.currentproduct.stock.id
      newItem.objedit = false
      if (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax) {
        newItem.fullPrice = (newItem.fullPrice - newItem.tax).toFixed(2)
        newItem.fullPriceWithoutDiscount = newItem.fullPriceWithoutDiscount - ((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) * newItem.fullPriceWithoutDiscount).toFixed(2)
      }
      this.tableItems.splice(this.currentIndex, 1, newItem)
      // var addQuantantiy = rowData.newquantity - oldObject.quantity
      // var newObject = oldObject
      // newObject.qu = rowData.newquantity
      // newObject.quantity = rowData.newquantity
      // if (oldObject.isPercentage && oldObject.disscountPercentage != null) {
      //   newObject.fullPrice = parseFloat(oldObject.price) * parseFloat(newObject.qu)
      //   newObject.tax = ((oldObject.product.taxGroup.value / 100) * newObject.fullPrice).toFixed(2)
      //   newObject.priceAfterTax = (oldObject.product.taxGroup.valueHaveTax ? parseFloat(newObject.fullPrice) : parseFloat(newObject.fullPrice) + parseFloat(((oldObject.product.taxGroup.value / 100) === 0 ? 1 : (oldObject.product.taxGroup.value / 100)) * (newObject.fullPrice))).toFixed(2)
      // } else {
      //   newObject.fullPrice = parseFloat(oldObject.price) * parseFloat(newObject.qu)
      //   newObject.tax = ((oldObject.product.taxGroup.value / 100) * newObject.fullPrice).toFixed(2)
      //   newObject.priceAfterTax = (oldObject.product.taxGroup.valueHaveTax ? parseFloat(newObject.fullPrice) : parseFloat(newObject.fullPrice) + parseFloat(((oldObject.product.taxGroup.value / 100) === 0 ? 1 : (oldObject.product.taxGroup.value / 100)) * (newObject.fullPrice))).toFixed(2)
      //   newObject.disscountPercentage = 100 - ((newObject.fullPrice / (parseFloat(oldObject.price) * parseFloat(newObject.qu))) * 100)
      // }
      // this.tableItems.splice(rowIndex, 1, newObject)
    },
    okCreatefunProductPrice () {
      try {
        this.stopBtn = true
        if (this.UintAndPriceByCUstomer.currentproduct < this.UintAndPriceByCUstomer.valueQuantaty) {
          var check = confirm(this.$t('generic.invOutOfStockMsg'))
          if (check) {
            this.createModalProductPrice = false
            this.UintAndPriceByCUstomer.valueUint = ''
            this.UintAndPriceByCUstomer.currentproduct = ''
            this.UintAndPriceByCUstomer.valuePrice = 0
            this.UintAndPriceByCUstomer.valueQuantaty = 1
            this.UintAndPriceByCUstomer.discountByPercentage = 0.0
            this.UintAndPriceByCUstomer.discountByPound = 0.0
            this.stopBtn = false
            return this.showToast(this.$t('generic.canceelAddMsg'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
        }
        var newItem = {
          fullName: this.UintAndPriceByCUstomer.currentproduct.nameAr,
          quantity: this.UintAndPriceByCUstomer.valueQuantaty,
          uints: this.UintAndPriceByCUstomer.valueUint.uint_Name,
          price: this.UintAndPriceByCUstomer.valuePrice,
          fullPrice: (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty),
          fullPriceWithoutDiscount: (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty),
          productToEdite: Object.assign({}, this.UintAndPriceByCUstomer),
          qu: this.UintAndPriceByCUstomer.valueQuantaty,
          isPercentage: false,
          disscountPercentage: null,
          productid: this.UintAndPriceByCUstomer.currentproduct.id,
          product: this.UintAndPriceByCUstomer.currentproduct,
          uintid: this.UintAndPriceByCUstomer.valueUint.fk_Uint,
          tax: ((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) * (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty)).toFixed(2),
          taxValue: (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100),
          priceAfterTax: (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax ? (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty) : (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty) + (((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) === 0 ? 1 : (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100)) * (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty))).toFixed(2),
          stockid: this.UintAndPriceByCUstomer.currentproduct.stock.id,
          objedit: false,
        }
        if (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax) {
          newItem.fullPrice = (newItem.fullPrice - newItem.tax).toFixed(2)
        }
        var getindex = this.tableItems.findIndex(x => x.productid === this.UintAndPriceByCUstomer.currentproduct.id && x.uintid === this.UintAndPriceByCUstomer.valueUint.fk_Uint)
        if (getindex !== -1) {
          var oldObject = this.tableItems[getindex]
          var updatedObject = newItem
          updatedObject.qu = parseFloat(newItem.qu) + parseFloat(oldObject.qu)
          updatedObject.quantity = updatedObject.qu
          updatedObject.isPercentage = oldObject.isPercentage
          updatedObject.disscountPercentage = oldObject.disscountPercentage
          if (oldObject.disscountPercentage !== null) {
            if (oldObject.isPercentage) {
              updatedObject.fullPrice = oldObject.disscountPercentage === null ? (newItem.price * updatedObject.quantity) : ((newItem.price * updatedObject.quantity) - ((oldObject.disscountPercentage / 100) * (newItem.price * updatedObject.quantity)))
            } else {
              var discountByPound = (oldObject.disscountPercentage / 100) * (oldObject.price * oldObject.quantity)
              updatedObject.disscountPercentage = ((discountByPound / (newItem.price * updatedObject.quantity)) * 100)
              updatedObject.fullPrice = (newItem.price * updatedObject.quantity) - discountByPound
            }
          } else {
            updatedObject.fullPrice = oldObject.disscountPercentage === null ? (newItem.price * updatedObject.quantity) : ((newItem.price * updatedObject.quantity) - ((oldObject.disscountPercentage / 100) * (newItem.price * updatedObject.quantity)))
          }
          updatedObject.fullPriceWithoutDiscount = parseFloat(updatedObject.price) * parseFloat(updatedObject.qu)

          updatedObject.productToEdite.valueQuantaty = updatedObject.productToEdite.valueQuantaty + oldObject.productToEdite.valueQuantaty
          updatedObject.tax = ((oldObject.product.taxGroup.value / 100) * updatedObject.fullPrice).toFixed(2)
          updatedObject.priceAfterTax = (oldObject.product.taxGroup.valueHaveTax ? (newItem.fullPrice) : (newItem.fullPrice) + (((newItem.product.taxGroup.value / 100) === 0 ? 1 : (newItem.product.taxGroup.value / 100)) * (updatedObject.fullPrice))).toFixed(2)
          if (oldObject.product.taxGroup.valueHaveTax) {
            updatedObject.fullPrice = (updatedObject.fullPrice - updatedObject.tax).toFixed(2)

            updatedObject.fullPriceWithoutDiscount = (updatedObject.fullPriceWithoutDiscount - ((oldObject.product.taxGroup.value / 100) * updatedObject.fullPriceWithoutDiscount).toFixed(2)).toFixed(2)
          }
          this.tableItems.splice(getindex, 1, updatedObject)
        } else {
          this.tableItems.push(newItem)
        }

        this.createModalProductPrice = false
        this.UintAndPriceByCUstomer.valueUint = ''
        this.UintAndPriceByCUstomer.currentproduct = ''
        this.UintAndPriceByCUstomer.valuePrice = 0
        this.UintAndPriceByCUstomer.valueQuantaty = 1
        this.UintAndPriceByCUstomer.discountByPercentage = 0.0
        this.UintAndPriceByCUstomer.discountByPound = 0.0
        this.stopBtn = false
      } catch (error) {
        this.stopBtn = false
        console.error(error)
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
    cancleCreatefunProductPrice () {
      this.createModalProductPrice = false
      this.UintAndPriceByCUstomer.valueUint = ''
      this.UintAndPriceByCUstomer.valuePrice = 0
      this.UintAndPriceByCUstomer.valueQuantaty = 1
      this.UintAndPriceByCUstomer.discountByPercentage = 0.0
      this.UintAndPriceByCUstomer.discountByPound = 0.0
    },
    /* Delete Model */
    showDeleteModal (id) {
      this.deleteModal = true
      this.currentIndex = id
    },
    okDeletefun () {
      this.tableItems.splice(this.currentIndexs, 1)
    },
    cancleDeletefun () {
      this.deleteModal = false
    },
    /* End Delete Model */

    /* edit model */
    showEditModal (cuobject, index) {
      this.editModalProductPrice = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModalProductPrice = true
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.UintAndPriceByCUstomer.options = cuobject.productToEdite.options
      this.UintAndPriceByCUstomer.valueUint = cuobject.productToEdite.options.find(el => el.fk_Uint === cuobject.uintid)
      this.UintAndPriceByCUstomer.valuePrice = cuobject.price
      this.UintAndPriceByCUstomer.valueQuantaty = cuobject.quantity
      this.UintAndPriceByCUstomer.currentproduct = cuobject.product
      this.currentIndex = index
      if (cuobject.disscountPercentage !== null) {
        if (cuobject.isPercentage) {
          this.UintAndPriceByCUstomer.discountByPercentage = cuobject.disscountPercentage
        } else {
          this.UintAndPriceByCUstomer.discountByPound = (cuobject.disscountPercentage / 100) * (cuobject.price * cuobject.quantity)
        }
      }
    },
    okEditfunProductPrice () {
      try {
        this.stopBtn = true
        var disscountPercentage = null
        var isPercentage = false
        // eslint-disable-next-line eqeqeq
        if (this.UintAndPriceByCUstomer.discountByPercentage != 0) {
          disscountPercentage = this.UintAndPriceByCUstomer.discountByPercentage
          isPercentage = true
        // eslint-disable-next-line eqeqeq
        } else if (this.UintAndPriceByCUstomer.discountByPound != 0) {
          disscountPercentage = ((this.UintAndPriceByCUstomer.discountByPound / (this.UintAndPriceByCUstomer.valuePrice * this.tableItems[this.currentIndex].quantity)) * 100)
          isPercentage = false
        }
        var newItem = {}
        newItem.fullPrice = disscountPercentage === null ? (this.UintAndPriceByCUstomer.valuePrice * this.tableItems[this.currentIndex].quantity) : ((this.UintAndPriceByCUstomer.valuePrice * this.tableItems[this.currentIndex].quantity) - ((disscountPercentage / 100) * (this.UintAndPriceByCUstomer.valuePrice * this.tableItems[this.currentIndex].quantity)))
        newItem.fullPriceWithoutDiscount = (this.UintAndPriceByCUstomer.valuePrice * this.tableItems[this.currentIndex].quantity)
        newItem.fullName = this.UintAndPriceByCUstomer.currentproduct.nameAr
        newItem.quantity = this.tableItems[this.currentIndex].quantity
        newItem.uints = this.UintAndPriceByCUstomer.valueUint.uint_Name
        newItem.price = this.UintAndPriceByCUstomer.valuePrice
        newItem.productToEdite = Object.assign({}, this.UintAndPriceByCUstomer)
        newItem.qu = this.tableItems[this.currentIndex].quantity
        newItem.productid = this.UintAndPriceByCUstomer.currentproduct.id
        newItem.product = this.UintAndPriceByCUstomer.currentproduct
        newItem.uintid = this.UintAndPriceByCUstomer.valueUint.fk_Uint
        newItem.isPercentage = disscountPercentage === null ? null : isPercentage
        newItem.disscountPercentage = disscountPercentage
        newItem.tax = ((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) * (newItem.fullPrice)).toFixed(2)
        newItem.taxValue = (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100)
        newItem.priceAfterTax = (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax ? (newItem.fullPrice) : (newItem.fullPrice) + (((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) === 0 ? 1 : (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100)) * (newItem.fullPrice))).toFixed(2)
        newItem.stockid = this.UintAndPriceByCUstomer.currentproduct.stock.id
        newItem.objedit = false
        if (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax) {
          newItem.fullPrice = (newItem.fullPrice - newItem.tax).toFixed(2)
          newItem.fullPriceWithoutDiscount = newItem.fullPriceWithoutDiscount - ((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) * newItem.fullPriceWithoutDiscount).toFixed(2)
        }
        this.tableItems.splice(this.currentIndex, 1, newItem)

        this.editModalProductPrice = false
        this.createModalProductPrice = false
        this.stopBtn = false
        this.UintAndPriceByCUstomer.valueUint = ''
        this.UintAndPriceByCUstomer.currentproduct = ''
        this.UintAndPriceByCUstomer.valuePrice = 0
        this.UintAndPriceByCUstomer.valueQuantaty = 1
        this.UintAndPriceByCUstomer.discountByPercentage = 0.0
        this.UintAndPriceByCUstomer.discountByPound = 0.0
      } catch (error) {
        this.stopBtn = false
        console.error(error)
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
    cancleEditfunProductPrice () {
      this.editModalProductPrice = false
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {}
      this.createModalProductPrice = false
      this.UintAndPriceByCUstomer.valueUint = ''
      this.UintAndPriceByCUstomer.currentproduct = ''
      this.UintAndPriceByCUstomer.valuePrice = 0
      this.UintAndPriceByCUstomer.valueQuantaty = 1
      this.UintAndPriceByCUstomer.discountByPercentage = 0.0
      this.UintAndPriceByCUstomer.discountByPound = 0.0
    },
    /* end edit model */

    /* finish  */
    showsubmitModdal () {
      if (this.tableItems.length >= 1) {
        this.modelTitle = 'generic.finishBill'
        this.modelOktext = 'modal.ok'
        this.submitModdal = true
        this.ApiRequest.inv_dueDate = helper.todayDate()
      } else {
        this.showToast(this.$t('generic.emptyBil'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
    okSubmitfun () {
      this.stopBtn = true
      this.ApiRequest.total_Price = this.totalBilTax
      this.ApiRequest.total_Tax = this.totalTax
      this.ApiRequest.is_Cash = false
      this.ApiRequest.liveStatus = 'tostock'
      this.ApiRequest.date = helper.todayDate()
      this.ApiRequest.fK_Branchs = 2
      this.ApiRequest.fK_Customers = this.CustomerId
      this.ApiRequest.fK_Inv_type = 1
      this.ApiRequest.fK_Safes = 1
      this.ApiRequest.fK_Inv_PayType = 1
      this.ApiRequest.fK_Pay_Type = 1
      this.ApiRequest.fK_Employees = 1
      this.ApiRequest.disscountPercentage = this.invDiscount
      this.tableItems.forEach(element => {
        var newSubs = {
          quantity: element.qu,
          price: element.price,
          tax: element.taxValue * 100,
          disscountPercentage: element.disscountPercentage === null ? 0 : element.disscountPercentage,
          fK_Products: element.productid,
          fK_Stock: element.stockid,
          fK_Units: element.uintid,
        }
        console.log(element)
        this.ApiRequest.inv_Subs.push(newSubs)
      })
      console.log(this.ApiRequest)
      BillService.create(this.ApiRequest).then(res => {
        // eslint-disable-next-line eqeqeq
        if (res.status == 200) {
          this.ApiRequest = {
            inv_Subs: [],
          }
          this.submitModdal = false
          this.countDownModdal = true
          this.invId = res.data.inv_id !== null ? res.data.inv_id : '----'
          setInterval(() => {
            this.countDown -= 1
          }, 1000)
          setTimeout(() => {
            location.reload()
          }, 3000)
        // eslint-disable-next-line eqeqeq
        } else if (res.status == 210) {
          alert(this.$t('generic.' + res.data.message))
          this.loading = false
          this.stopBtn = false
          this.submitModdal = false
        }
      },
      err => {
        // eslint-disable-next-line eqeqeq
        if (err.status == 409) {
          alert(this.$t('generic.' + err.data))
        } else {
          console.error(err)
          this.loading = false
          this.stopBtn = false
          this.submitModdal = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      })
    },
    canclesubmitfun () {
      this.submitModdal = false
      this.invDiscountByPercentage = 0
      this.invDiscountByPound = 0
      this.ApiRequest = {}
    },
    /* end finish */
  },
}

</script>

